import { Component } from '@angular/core';
import { Home } from '../../home/home';
import { Values } from '../../../providers/service/values';
import { NavController, AlertController, IonicPage} from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Functions } from '../../../providers/service/functions';
import { CheckoutService } from '../../../providers/service/checkout-service';


@IonicPage()
@Component({
  templateUrl: 'order-summary.html',
})
export class OrderSummary {

  status: any;
  buttonText: any;
  orderSummary: any;
  public disableSubmit: boolean = false;
  
  
  constructor(public nav: NavController, public service: CheckoutService, public values: Values, public functions: Functions, public alert: AlertController, private inAppBrowser: InAppBrowser ) {

    this.buttonText = this.values.lan.text_confirm_order;  
  	
    this.service.getOrderSummary()
    .then((results) => this.orderSummary = results);  
  }

  
  saveOrder(){

    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText= this.values.lan.text_loading;   

      if(this.orderSummary.payment_code == 'pp_pro'){
        this.service.savePpPro(this.orderSummary)
          .then((results) => this.save(results));
      }

      else if(this.orderSummary.payment_code == 'authorizenet_aim'){
        this.service.saveAuthoriseNet(this.orderSummary)
          .then((results) => this.save(results));
      }
 
      else if(this.orderSummary.payment_code == 'cod' || this.orderSummary.payment_code == 'bank_transfer' || this.orderSummary.payment_code == 'cheque' || this.orderSummary.payment_code == 'skrill'){
        
        this.service.saveOrder(this.orderSummary.payment_code)
         .then((results) => this.handleResults(results));
      }

      else if(this.orderSummary.payment_code == 'pp_standard'){
        let browser =  this.inAppBrowser.create(this.orderSummary.payment.redirect, '_blank', 'location=yes');
        browser.show();
        this.values.count = 0;
        this.nav.setRoot(Home);

      }

      else if(this.orderSummary.payment_code == 'payu'){
        var payment = this.orderSummary.payment;
        var url = payment.action + '?key=' + payment.key + '&txnid=' + payment.txnid + '&amount=' + payment.amount + '&productinfo=' + payment.productinfo + '&firstname=' + payment.firstname + '&Lastname=' + payment.Lastname + 
        '&Zipcode=' + payment.Zipcode + '&email=' + payment.email + '&phone=' + payment.phone + '&surl=' + payment.surl + '&furl=' + payment.furl + 
        '&curl=' + payment.curl + '&Hash=' + payment.Hash + '&Pg=' + payment.Pg + '&service_provider=' + payment.service_provider + '&address1=' + payment.address1 + 
        '&address2=' + payment.address2 + '&city=' + payment.city + '&country=' + payment.country + '&state=' + payment.state;

        console.log(url);




        let browser = this.inAppBrowser.create(url, '_blank', 'location=yes');
        browser.show();
        this.values.count = 0;
        this.nav.setRoot(Home);

      }
    }
  }  
  
 
  save(results){
    this.service.saveOrder(this.orderSummary.payment_code)
    .then((results) => this.handleResults(results));
  }

  
  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.values.lan.text_confirm_order;

    if(results.status){
      this.values.count = 0;
      this.nav.setRoot(Home);
    }

    let alert = this.alert.create({
      title: results.heading_title,
      subTitle: results.text,
      buttons: [results.button_continue]
    });
    alert.present(alert);
  }

 validateForm(){
  
        if(this.orderSummary.payment_code == 'pp_pro'){
          if(this.orderSummary.payment.cc_type == undefined){this.functions.showAlert(this.values.lan.text_error, "Please Select Card Type"); return false}
          if(this.orderSummary.payment.cc_number == undefined || this.orderSummary.payment.cc_number == ""){this.functions.showAlert(this.values.lan.text_error, "Please Type Card Number"); return false}
          if(this.orderSummary.payment.cc_expire_date_month == undefined){this.functions.showAlert(this.values.lan.text_error, "Please Select Card Expire Month"); return false}
          if(this.orderSummary.payment.cc_expire_date_year == undefined){this.functions.showAlert(this.values.lan.text_error, "Please Select Card Expire Year"); return false}
          if(this.orderSummary.payment.cc_cvv2 == undefined || this.orderSummary.payment.cc_cvv2 == ""){this.functions.showAlert(this.values.lan.text_error, "Please Enter Card CVV Number"); return false}
        }

        else if(this.orderSummary.payment_code == 'authorizenet_aim'){
          if(this.orderSummary.payment.cc_owner == undefined || this.orderSummary.payment.cc_owner == ""){this.functions.showAlert(this.values.lan.text_error, "Please Enter Card Owner Name"); return false} 
          if(this.orderSummary.payment.cc_number == undefined || this.orderSummary.payment.cc_number == ""){this.functions.showAlert(this.values.lan.text_error, "Please Type Card Number"); return false}
          if(this.orderSummary.payment.cc_expire_date_month == undefined){this.functions.showAlert(this.values.lan.text_error, "Please Select Card Expire Month"); return false}
          if(this.orderSummary.payment.cc_expire_date_year == undefined){this.functions.showAlert(this.values.lan.text_error, "Please Select Card Expire Year"); return false}
          if(this.orderSummary.payment.cc_cvv2 == undefined || this.orderSummary.payment.cc_cvv2 == ""){this.functions.showAlert(this.values.lan.text_error, "Please Enter Card CVV Number"); return false}
        }

      return true;
  
  }

}



